import React from 'react';
import { Form, ButtonToolbar, Button, Input } from 'rsuite';
import 'rsuite/dist/rsuite.min.css';
import './App.css';
import ky from 'ky';
const Textarea = React.forwardRef((props, ref) => <Input {...props} as="textarea" ref={ref} />);

export default class NameForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { question: '',
                 };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  async handleSubmit(event) {
      event.preventDefault();
      try {
         const response = await ky.post('https://netbot.artraf.ru/api/ask', {json:
                                       {question: this.state.question}});
         if(response.status === 200) {
            const json = await response.json()
            await this.setState({ question: "Bot: " + json["ans"]});
         }
      } catch (error) { }
    }
  render() {
  return (
  <div className="App">
  <Form className="form-signin" onSubmit={this.handleSubmit}>
    <Form.Group controlId="textarea">
      <Form.ControlLabel>Pogadaj z robocikiem </Form.ControlLabel>
      <Form.Control rows={5} name="textarea" accepter={Textarea}
      value={this.state.question}
      onChange={(value) => this.setState({ question: value })} />
    </Form.Group>
    <Form.Group>
      <ButtonToolbar>
        <Button appearance="primary" onClick={this.handleSubmit}>Submit</Button>
        <Button appearance="default">Cancel</Button>
      </ButtonToolbar>
    </Form.Group>
  </Form>
    </div>
  );
 }
}

